export default {
  name: 'UniSwiperDot',
  props: {
    info: {
      type: Array,
      default () {
        return []
      }
    },
    current: {
      type: Number,
      default: 0
    },
    dotsStyles: {
      type: Object,
      default () {
        return {}
      }
    },
    // 类型 ：default(默认) indexes long nav
    mode: {
      type: String,
      default: 'default'
    },
    // 只在 nav 模式下生效，变量名称
    field: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      dots: {
        width: 8,
        height: 8,
        bottom: 10,
        color: '#fff',
        backgroundColor: 'rgba(0, 0, 0, .3)',
        border: '1px rgba(0, 0, 0, .3) solid',
        selectedBackgroundColor: '#333',
        selectedBorder: '1px rgba(0, 0, 0, .9) solid'
      }
    }
  },
  watch: {
    dotsStyles (newVal) {
      this.dots = Object.assign(this.dots, this.dotsStyles)
    },
    mode (newVal) {
      if (newVal === 'indexes') {
        this.dots.width = 20
        this.dots.height = 20
      } else {
        this.dots.width = 8
        this.dots.height = 8
      }
    }

  },
  created () {
    if (this.mode === 'indexes') {
      this.dots.width = 20
      this.dots.height = 20
    }
    this.dots = Object.assign(this.dots, this.dotsStyles)
  }
}
